import BearKiller from "../assets/bearkilla.svg";
import Plus from "../assets/btn-plus.svg";
import Minus from "../assets/btn-minus.svg";
import { Helmet } from "react-helmet"

const freeAmount = 6777;

const getWindow = () => window !== undefined && window;

const getWindowEth = () => getWindow() && window.ethereum;

const getWhitelistProof = (address: string, type: string) => {
  return fetch(`/whitelist/${type}/${address}.json`)
    .then((e) => e.json())
    .then((e) => {
      return e.proof;
    });
};

const requestConnectWallet = async () => {
  const eth = getWindowEth();
  if (eth) {
    try {
      const provider = new ethers.providers.Web3Provider(eth);
      await provider.send("eth_requestAccounts", []);
      return await provider.getSigner();
    } catch (e) {
      console.log(e);
    }
  }
};

const mint = async (contract: any, walletAddress: any, quantity, value = 0) => {
  return contract?.mint(quantity, { from: walletAddress, value });
};

const callContract = async (contract: any, method: any, param: any) => {
  if (param) {
    return await contract[method](param);
  } else {
    return await contract[method]();
  }
};

