// markup
const IndexPage = () => {
  const size = React.useContext(ResponsiveContext);
  const [contract, setContract] = useState<Undead | null>(null);
  const [walletAddress, setWalletAddress] = useState<string | null>(null);
  const [proof, setProof] = useState<string | null>(null);
  const [mintQuantity, setMintQuantity] = useState<number>(1);
  const [myNFT, setMyNFT] = useState<[]>([]);
  const [nftMeta, setNftMeta] = useState<[]>([]);
  const [message, setMessage] = useState<string>("");
  const [title, setTitle] = useState<string>(
    `We are the undead of the bear market,`
  );
  const [titleTwo, setTitleTwo] = useState<string>(
    "What doesn't rekt you make you stronger"
  );

  const [mobileTitle, setMobileTitle] = useState<string>(
    "We are the undead of the bear market,What doesn't rekt you make you stronger"
  );

  const [subTitle, setSubtitle] = useState<string>(
    `First ${freeAmount} NFTs free, then 0.0033 ETH`
  );

  const [bottomCopy, setBottomCopy] = useState<string>(
    "It's dangerous to go alone! Take my hand."
  );
  const [mintQty, setMintQty] = useState<string>("0");
  const [currentSupply, setCurrentSupply] = useState(101);
  const [count, setCount] = useState<number>(1);
  const [total, setTotal] = useState<number>(0);

  const [bearkillaSpeed, setBearKillaSpeed] = useState("shakeSlower");
  const [handShake, setHandShake] = useState("");
  const [messageShake, setMessageShake] = useState("");
  const [totalSupply, setTotalSupply] = useState(0);

  useEffect(() => {
    const win = getWindow();
    //initializeContract();
    document.addEventListener("mousemove", () => {
      const audio = document.getElementById("success");
      if (!audio.paused) {
        audio.volume = 0.3;
        audio?.play();
      }
    });
  }, []);

  const initializeContract = async () => {
    const signer = await requestConnectWallet();
    if (signer) {
      signer.getAddress().then((address) => {
        setWalletAddress(address.toLowerCase());
      });

      const contractObject = new ethers.Contract(
        contractAddress,
        contractABI,
        signer
      ) as Undead;
      setContract(contractObject);
      callContract(contractObject, "totalSupply", "").then((e) => {
        setCurrentSupply(e.toNumber());
        contractObject.MAX_SUPPLY().then((e) => setTotalSupply(e.toNumber()));
      });
    }
  };

  useEffect(() => {
    if (walletAddress && contract) {
      callContract(contract, "tokensOf", walletAddress).then((e) => {
        setMintQty(e.length);
      });
    }
  }, [walletAddress, contract]);

  const mintUndead = (contract, walletAddress, quantity) => {
    if (walletAddress) {
      let price = 0;
      if (currentSupply + 1 > freeAmount) {
        price = 3300000000000000;
      }
      console.log(price);
      mint(contract, walletAddress, quantity, quantity * price)
        .then((success) => {
          console.log(success);
          const audio = document.getElementById("success");
          audio.volume = 0.3;
          audio?.play();

          callContract(contract, "tokensOf", walletAddress).then((e) => {
            setMintQty(e.length);
          });
        })
        .catch((e) => {
          console.log(e);
          if (e.code === "UNPREDICTABLE_GAS_LIMIT") {
            setMessage("dead already? check if you have enough eth?");
          }
        });
    } else {
      setMessage("Please connect wallet first");
    }
  };
  const incrementCount = () => {
    if (count >= 3) {
      console.log("maximum count is 3");
      return;
    } else {
      setCount(count + 1);
    }
  };

  const decrementCount = () => {
    if (count <= 0) {
      console.log("cannot go beyond 0");
      return;
    } else {
      setCount(count - 1);
    }
  };

  useEffect(() => {
    setMessageShake("shakeFaster");
    setTimeout(() => {
      setMessageShake("");
    }, 500);
  }, [message]);
  return (
    <Layout>
      <Helmet>
        <title>We are the undead of the bear market | Undeadtown.wtf</title>
      </Helmet>
      <div className="container">
        <audio autoPlay={true} id="background" controls loop hidden>
          <source src="/assets/background_music.wav" type="audio/ogg" />
          Your browser does not support the audio element.
        </audio>

        <audio id="success" autoPlay={true} controls hidden>
          <source src="/assets/success.wav" type="audio/ogg" />
          Your browser does not support the audio element.
        </audio>
        <div
          className="wallet"
          onClick={() => {
            initializeContract();
            setMessage("");
          }}
        >
          {walletAddress ? "Connected" : "Connect wallet"}
          <div className="message"> </div>
        </div>
        <ResponsiveContext.Consumer>
          {(size) => {
            return size === "small" ? (
              <div className="flex-row">
                <div className="moon">
                  <Logo className="logo"></Logo>
                </div>
                <div className="content-container">
                  <div className="mobileTitle">
                    <p>{title}</p>
                    <p>{titleTwo}</p>
                  </div>
                  <div className="mobileSubtitle">{subTitle}</div>
                  <div className="mobileSubtitle">
                    Free mint: max 1. You only have 1 soul to sacrifice, don't
                    be dumdum
                  </div>
                </div>
                <div className="supply-container">
                  {totalSupply && (
                    <div className="figure">
                      {currentSupply} minted/ {totalSupply} Supply
                    </div>
                  )}
                </div>
                <div className="mint-selection-container">
                  <Minus
                    className="minus"
                    onClick={() => decrementCount()}
                  ></Minus>
                  <div className="counter">{"" && count}</div>
                  <Plus
                    className="plus"
                    onClick={() => incrementCount()}
                  ></Plus>
                </div>
                <div className="hand-c">
                  <div className="message"></div>
                  <Hand
                    className={`hand buttonSVG ${handShake}`}
                    onClick={() => mintUndead(contract, walletAddress, count)}
                  ></Hand>
                </div>
                <div className="minted-container">
                  <div className={`mobileBottom-copy ${messageShake}`}>
                    {message || bottomCopy}
                  </div>
                  <div className="mint-confirmed">
                    <p>ARRRR {mintQty} MINTED</p>
                  </div>
                </div>
                <div className="luna-c">
                  <Luna className="luna"></Luna>
                </div>
              </div>
            ) : (
              <>
                <div className="flex-row">
                  <div className="moon">
                    <Logo className="logo"></Logo>
                  </div>
                  <div className="content-container">
                    <div className="title">
                      <p>{title}</p>
                      <p>{titleTwo}</p>
                    </div>
                    <div className="subtitle">{subTitle}</div>

                    <div className="subtitle">
                      Free mint: max 1. You only have 1 soul to sacrifice, don't
                      be dumdum
                    </div>
                  </div>
                  <div className="supply-container">
                    {totalSupply && (
                      <div className="figure">
                        {currentSupply} minted/ {totalSupply} Supply
                      </div>
                    )}
                  </div>
                  <div className="mint-selection-container">
                    <Minus
                      className="minus"
                      onClick={() => decrementCount()}
                    ></Minus>
                    <div className="counter">{"" && count}</div>
                    <Plus
                      className="plus"
                      onClick={() => incrementCount()}
                    ></Plus>
                  </div>
                  <div className="luna-c">
                    <div className="message"> </div>
                    <Luna className="luna"></Luna>
                    <Hand
                      className={`hand buttonSVG ${handShake}`}
                      onClick={() => mintUndead(contract, walletAddress, count)}
                    ></Hand>

                    <BearKiller
                      className={`bearkilla ${bearkillaSpeed}`}
                    ></BearKiller>
                  </div>
                  {mintQty && (
                    <div className="minted-container">
                      <div className={`bottom-copy ${messageShake}`}>
                        {" "}
                        {message || bottomCopy}
                      </div>

                      <div className="mint-confirmed">
                        <p>ARRRR {mintQty} MINTED</p>
                      </div>
                    </div>
                  )}

                  {/* <div className="o-c">
                    <Luna className="luna"></Luna>

                    <BearKiller class="bearkilla"></BearKiller>
                  </div> */}
                </div>
              </>
            );
          }}
        </ResponsiveContext.Consumer>
      </div>
    </Layout>
  );
};

export default IndexPage;
